body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-lato: Lato;

  /* font sizes */
  --font-size-17xl: 36px;
  --font-size-13xl: 32px;
  --font-size-xl: 20px;
  --font-size-5xl: 24px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-cornflowerblue-100: #7194d8;
  --color-cornflowerblue-200: #6794ec;
  --color-azure: #eafafa;

  /* Border radiuses */
  --br-xl: 20px;
  --br-3xs: 10px;
}
